    box-sizing: border-box;
}

body {
    background-color: lightblue;
}

h1 {
    margin-left: 30px;
}

.content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

input {
    height: 30px;
    border: 2px solid dodgerblue;
    border-radius: 5px;
    text-align: center;
}

.display {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 325px;
    height: 420px;
    margin-top: 25px;
    border: 5px solid rgb(150, 150, 150);
    border-radius: 10%;
    background-color: darkcyan;
;
    /* background-color: rgb(175, 254, 175); */
}

.container {
    display: flex;
    flex-direction: column;
    justify-content: start;
    align-items: center;
    padding: 35px;
    padding-top: 15px;
}

.container div {
    font-size: 25px;
    margin: 15px;
    color: white;
}

.container > div:first-child {
    font-size: 35px;
    margin: 20px;
    margin-top: 10px;
    color: white;
}

.display img {
    margin-top: 20px;
